.item {
    width: 100%;
    height: 48px;
    display: flex;
    align-items: center;
    padding: 16px 0 16px 0;
    border-bottom: 1px solid #E5E5E5;
}

.item:last-of-type {
    border-bottom: none;
}

.users {
    margin-top: 19px;
}

.buttonsContainer {
    width: 606px;
    display: flex;
    justify-content: flex-end;
}

.container {
    padding: 34px 0 0 70px;
}

.thead tr {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    width: 606px;
    height: 36px;
    background: #E5E5E5;
    display: flex;
    align-items: center;
}

.thead tr td:first-child {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    width: 50%;
}

.nameCol {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    padding-left: 17px;
    border-bottom: #E5E5E5;
    color: #333;
    width: 50%;
}

td.ageCol {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;

    color: #333;
}
